import React from 'react'
import styles from './AuditCard.module.css'

const AuditCard = ({extrastyles, chainid}) => {
    return (
        <div class={`${styles.body} ${extrastyles}`}>
            <div class={styles.lock} />
            <div class={`${styles.chainlogo} ${chainid == 1 ? styles.bsclogo : 0} ${chainid == 2 ? styles.polygonlogo : 0}`} />
            <a class={styles.title}>AUDITARY</a>
            <a class={styles.subtitle}>SMART CONTRACT AUDIT</a>
            <a class={`${styles.subtitle} ${styles.extramargin}`}>NAME: AUDIARY MASTER</a>
            <a class={styles.subtitle}>0x99b87a9cf87e97887d9a98b7...</a>
            <a class={styles.bottomtext}>0 HIGH RISK VULNERABILITIES</a>
        </div>
    )
}

export default AuditCard
